.App {
  text-align: center;
  background-color: #e9f5eb;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.input-prompt {
  text-align: center;
  font-weight: string;
  font-size: 1.4em;
  padding-bottom: 1em;
}

.navigation {
  padding-top: 1em;
  padding-bottom: 1em;
}

.button-group-container {
 background-color:#88c9A1;
}

btn.btn-submit {
  padding-top: 10px
}

.button-row button {
  background-color: white;
  border: 1px solid green; /* Green border */
  padding: 10px 24px; /* Some padding */
  cursor: pointer; /* Pointer/hand icon */
  float: left; /* Float the buttons side by side */
  color: #6E661B;
  border-radius: 8px;
  font-size: 16px;
  width: 100%;
}

.button-row button:not(:last-child) {
  border-right: none; /* Prevent double borders */
}

/* Clear floats (clearfix hack) */
.button-row:after {
  content: "";
  clear: both;
  display: table;
}

/* Add a background color on hover */
.button-row button:hover {
  background-color: #D8F1E1;
}

.input-bill-amount.wrapper {
  width: 300px;
  height:300px;
}

input[type=text] {
  border: none;
  cursor: text;
  outline: none;
  border-bottom: 2px solid #6E661B;
  display: block;
  margin : 0 auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* This prevents double-tap zooming on iOS */
touch-action: manipulation;
